import React, {Component} from 'react';
import {Table} from 'antd';
import {Row, Col} from 'antd';
import fetch from 'isomorphic-fetch';
import PropTypes from 'prop-types'

class OrderList extends Component {

    constructor() {
        super();
        this.state = {
            orders: [],
            goodsInfos: [],
            product: [],
            /*            createTimes: [],
             sns: [],
             buyer_partners: [],
             addresses: [],
             required_dates: []*/
        };
        this.getData = this.getData.bind(this);
    }

    componentWillMount() {
        this.getData();
    }

//获取数据
    getData() {
        let opts = {
            method: "GET",
        };
        /*        let createTimes = [];
         let sns = [];
         let buyer_partners = [];
         let addresses = [];
         let required_dates = [];
         let products = [];*/
        let selfthis = this;
        fetch('//192.168.1.159:8000/api/orders/?format=json', opts)
            .then(function (response) {
                return response.json();
            }).then(function (jsonData) {
            console.log(jsonData.results);
            selfthis.setState({
                orders: jsonData.results,
                /*                createTimes: jsonData.results.map((order) => {
                 let time = new Date(order.create_time);
                 let year = time.getFullYear();
                 let month = time.getMonth() + 1;
                 let date = time.getDate();
                 createTimes.push(year + '年' + month + '月' + date + '日');
                 return createTimes;
                 }),
                 sns: jsonData.results.map((order) => {
                 sns.push(order.sn);
                 return order.sn;
                 }),
                 buyer_partners: jsonData.results.map((order) => {
                 buyer_partners.push(order.buyer_partner);
                 return order.buyer_partners;
                 }),
                 addresses: jsonData.results.map((order) => {
                 addresses.push(order.address);
                 return addresses;
                 }),
                 required_dates: jsonData.results.map((order) => {
                 required_dates.push(order.required_date);
                 return required_dates;
                 }),
                 products: jsonData.results.map((order) => {
                 order.items.map((item) => {
                 products.push(item.product);
                 return products;
                 })
                 })*/
            });
            /*            fetch('http://192.168.1.159:8000/api/products/296/?format=json', opts)
             .then(function (response) {
             return response.json();
             }).then(function (jsonData) {
             console.log(jsonData);
             selfthis.setState({
             goodsInfos: jsonData
             })
             })*/
        }).catch(function (error) {
            alert(error);
        });
    }

    render() {
        return (
            <div className="order-lists">
                <div className="order-list">
                    {this.state.orders.map((order, index) => {
                        return (
                            <div className="order-list" key={index}>
                                <Row className="order-head" gutter={16}>
                                    <Col span={4}>
                                        <input type="checkBox"/>
                                        创建日期:{new Date(order.create_time).getFullYear() + '年'
                                    + new Date(order.create_time).getMonth() + '月'
                                    + new Date(order.create_time).getDate() + '日'}
                                    </Col>
                                    <Col span={4}>订单号:{order.sn}</Col>
                                    <Col span={4}>订单金额:{order.items.map((item) => {
                                        return item.quantity * item.all_cost
                                    })}</Col>
                                    <Col span={4}>客户:{order.buyer_partner}</Col>
                                    <Col span={4}>收货地址:{order.address}</Col>
                                    <Col span={4}>需求日期:{order.required_date}</Col>
                                </Row>
                                {order.items.map((item, index) => {
                                    return (
                                        <Row className="order-item" key={index} gutter={16}>
                                            <Col span={2}>
                                                <div className="image-item">
                                                    <img src={item.image_url} style={{width: "100px", height: "100px"}}
                                                         alt="商品图片"></img>
                                                </div>
                                            </Col>
                                            <Col span={5} className="goods-info">
                                                <div>{item.product_name}</div>
                                                <div>商品编码：{item.slug}</div>
                                                <div>物料编码：</div>
                                            </Col>
                                            <Col span={3} className="goods-unit">
                                                <span>数量:{item.quantity}</span>
                                                <span>(单位:{item.unit})</span>
                                            </Col>
                                            <Col span={4} className="goods-memo">备注：{item.memo}</Col>
                                            <Col span={4} className="goods-status">
                                                <span className="goods-count">{item.to_delivery}</span>
                                                {this.state.orders.map((order, index) => {
                                                    if (order.status_ext[0] == 0) {
                                                        return (<span key={index}>待发货</span>)
                                                    }
                                                })}
                                            </Col>
                                            <Col span={3} className="money">金额：{item.all_cost}</Col>
                                            <Col span={3} className="delivery-status">
                                                <button type="button" className="btn btn-default">接单</button>
                                                <span style={{display: "block"}}><a
                                                    href="/111">订单详情</a></span>
                                            </Col>
                                        </Row>
                                    )
                                })}
                            </div>
                        )
                    })}
                </div>
            </div>
        )
    }
}

export default OrderList;



