import React, {Component} from 'react';
import {Tabs} from 'antd';
const TabPane = Tabs.TabPane;

class OrderStatus extends Component {
    render() {
        return (
            <div className="order-status">
                <Tabs>
                    <TabPane tab="全部订单" key="1"></TabPane>
                    <TabPane tab="待确认" key="2"></TabPane>
                    <TabPane tab="代发货" key="3"></TabPane>
                    <TabPane tab="待收货" key="4"></TabPane>
                    <TabPane tab="代开发票" key="5"></TabPane>
                    <TabPane tab="代收款" key="6"></TabPane>
                    <TabPane tab="已完成" key="7"></TabPane>
                </Tabs>
            </div>
        )
    }
}

export default OrderStatus;


